import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import { getUUID } from "../actions/fakturAction";
import FormTerimaMasak from "./Component/FormTerimaMasak";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { getTableTerimaMasak } from "../actions/TerimaMasakAction";
import {
  finishSend,
  getBruto,
  getNetto,
  getQty,
  progressSend,
} from "../actions/LogicAction";
import Axios from "axios";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";
import { reset } from "redux-form";
import ReactTooltip from "react-tooltip";
import { deleteIndex } from "../deleteLocalStorage";
import HeaderCard from "../HeaderCard";

const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    listTerimaMasak: state.provinsi.listTerimaMasak,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumQty: state.perhitungan.sumQty,
    onSend: state.provinsi.onSend,
  };
};

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class TerimaMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      no: 0,
      no_kirim: "",
      kode_barang: "",
      kadar_cetak: "",
      bruto: 0,
      netto_terima: 0,
      netto_selisih: 0,
      netto_kirim: 0,
      status: "",
      tanggal: "",
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Model",
          footer: "",
        },
        {
          dataField: "kodeTukang",
          text: "Kode Tukang",
          footer: "",
        },
        {
          dataField: "kadarCetak",
          text: "Cap Kode",
          footer: "",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "nettoTerima",
          text: "Netto Terima(Gr)",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "nettoKirim",
          text: "Netto Kirim(Gr)",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "nettoSelisih",
          text: "Netto Selisih(Gr)",
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "terimaMasak",
                      rowIndex,
                      this.props.dispatch,
                      getTableTerimaMasak()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => console.log("EDIT")}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getTableTerimaMasak());
    this.props.dispatch(getUUID());
    this.props.dispatch(getNetto("terimaMasak"));
    this.props.dispatch(getBruto("terimaMasak"));
    this.props.dispatch(getQty("terimaMasak"));
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    if (localStorage.getItem("terimaMasak") === null) {
      warning("Data belum timbahkan, Silahkan Tambahkan data dahulu");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi tanggal");
      return false;
    }
    var data = {
      noFaktur: this.props.noFaktur.toUpperCase(),
      kodeLokasi: "PUSAT",
      noBon: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal.toUpperCase(),
      barangDetails: JSON.parse(localStorage.getItem("terimaMasak")),
      footerItem: {
        totalNetto: this.props.sumNetto,
        totalBruto: this.props.sumBruto,
        totalNettoExt: 0,
        totalP: 0,
        totalGross: 0,
        totalQty: this.props.sumQty,
        totalpkg: 0,
        totalRp: 0,
        totalK: 0,
      },
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("terimaMasak"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis,
        ticket.kodeTukang,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.nettoTerima,
        ticket.nettoKirim,
        ticket.nettoSelisih,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Jenis",
      "Kode Tukang",
      "Bruto",
      "Harga (%)",
      "Netto Terima",
      "Netto Kirim",
      "Netto Selisih",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "item/dc/terimamasak", data)
      .then(() =>
        Swal.fire({
          position: "top-right",
          icon: "success",
          text: "Transaksi Berhasil",
          timer: 1500,
          showConfirmButton: false,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "Tanggal",
              this.state.tanggal.toUpperCase(),
              "",
              "",
              "No Faktur",
              this.props.noFaktur.toUpperCase(),
              "",
              "",
              "ADMIN",
              this.state.tanggal.toUpperCase(),
              "",
              columTable,
              "BUKTI TERIMA MASAK",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("terimaMasak"))
          .then(() => this.props.dispatch(reset("FormTerimaMasak")))
          .then(() =>
            this.setState({
              tanggal: "",
            })
          )
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getTableTerimaMasak()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-right",
          icon: "error",
          text: "Sepertinya ada gangguan, Silahkan Check Koneksi anda",
          timer: 1500,
          showConfirmButton: false,
        })
          .then(() => console.log(err))
          .then(() => this.props.dispatch(finishSend()))
      );
  };

  handleSubmit(data) {
    var split = data.kodeBarang
      ? data.kodeBarang
      : "DEFUALT|DEFUALT|DEFAULT|DEFAULT";
    var explode = split.split("|");
    var simpan = {
      noFaktur: this.props.noFaktur.toUpperCase(),
      kadarTransaksi: parseFloat(data.kadarTransaksi),
      kodeTukang: data.kodeTukang.toUpperCase(),
      kodeLokasi: "PUSAT",
      kodeKategori: "-",
      kadarCetak: explode[1].toUpperCase(),
      kodeJenis: explode[0].toUpperCase(),
      noKirim: data.noKirim.toUpperCase(),
      brutoTerima: parseFloat(data.bruto),
      bruto: parseFloat(data.bruto),
      nettoTerima: parseFloat(data.nettoTerima),
      nettoKirim: parseFloat(data.nettoKirim) ? parseFloat(data.nettoKirim) : 0,
      nettoSelisih: parseFloat(data.nettoSelisih)
        ? parseFloat(data.nettoSelisih)
        : 0,
    };
    var array = JSON.parse(localStorage.getItem("terimaMasak")) || [];
    array.push(simpan);
    localStorage.setItem("terimaMasak", JSON.stringify(array));
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: "Data Berhasi Ditambahkan",
      timer: 1500,
      showConfirmButton: false,
    })
      .then(() => this.props.dispatch(getTableTerimaMasak()))
      .then(() => this.props.dispatch(reset("FormTerimaMasak")));
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Terima Masak" />
              <div className="card-body">
                <div className="row">
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tanggal"
                      className="form-control"
                      onChange={this.inputChange("tanggal")}
                      value={this.state.tanggal}
                    />
                  </div>
                  <div className="col-md-9 col-lg-9"></div>
                </div>
                <div className="col-lg-12 col-md-12 mb-3 mt-3">
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="text-left">
                        <button
                          className="btn btn-primary"
                          disabled={this.props.onSend}
                          onClick={() => this.submit()}
                        >
                          {this.props.onSend ? (
                            <span
                              className="spinner-border spinner-border-sm mr-3"
                              aria-hidden="true"
                            ></span>
                          ) : null}
                          Simpan
                          <i className="fa fa-paper-plane ml-3"></i>
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-target="#tambahModal"
                          data-toggle="modal"
                        >
                          Tambah Data
                          <i className="fa fa-plus ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                {this.props.listTerimaMasak ? (
                  <BootstrapTable
                    keyField="id"
                    data={this.props.listTerimaMasak}
                    columns={this.state.columns}
                    bordered={true}
                    headerClasses="header-table"
                  />
                ) : (
                  <BootstrapTable
                    keyField="id"
                    data={[]}
                    columns={this.state.columns}
                    bordered={true}
                    headerClasses="header-table"
                    noDataIndication="Tidak Ada Data"
                  />
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="tambahModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaMasak onSubmit={(data) => this.handleSubmit(data)} />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TerimaMasak);
