import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import Axios from "axios";
import Swal from "sweetalert2";
import validaitonTerimaMasak from "../../Validations/validationTerimaMasak";
import { getTimbangan } from "../../GetTimbangan";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormTerimaMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
      hasilBarang: [],
    };
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    Axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
    getTimbangan(this.props.change);
  }

  handleChange = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarJual: result_explode[2],
      namaJenis: result_explode[3],
    });
    this.props.change("kadar", result_explode[1]);
    this.props.change("kadarTransaksi", result_explode[2]);
    this.props.change("keterangan", result_explode[3]);
  };

  setNetto() {
    this.props.change(
      "nettoTerima",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
    this.props.change(
      "nettoSelisih",
      this.props.nettoSelisih === "NaN" ? 0 : this.props.nettoSelisih
    );
  }

  searchMasak = (input) => (e) => {
    console.log("MEMULAI PENCARIAN");
    console.log(e.target.value);
    Axios.get(
      serverNonMaster + "item/dc/kirimmasak/detail?noFaktur=" + e.target.value
    )
      .then((res) =>
        this.setState({
          hasilBarang: res.data.footerItem.totalNetto,
          kodeTukang: res.data.kodeLokasi,
          noKirim: res.data.noFaktur,
        })
      )
      .then((res) => this.props.change("nettoKirim", this.state.hasilBarang))
      .then(() => this.props.change("kodeTukang", this.state.kodeTukang))
      .then(() => this.props.change("noKirim", this.state.noKirim))
      .catch((err) =>
        Swal.fire({
          position: "top-right",
          icon: "error",
          text: "Barang Masak Tidak Ditemukan, periksa lagi Nomornya..",
          timer: 1500,
          showConfirmButton: false,
        }).then(() => console.log(err))
      );
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-md-6 col-lg-6 form-group">
              <label htmlFor="">Nomor Kirim</label>
              <input
                type="text"
                id="noKirim"
                name="noKirim"
                className="form-control"
                onBlur={this.searchMasak("kode")}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group d-none">
              <Field
                name="noKirim"
                label="Nomor Kirim"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group d-none">
              <Field
                name="kodeTukang"
                label="Nomor Kirim"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group">
              <label htmlFor="">Kode Model</label>
              <Field
                component="select"
                name="kodeBarang"
                className="form-control"
                onChange={this.handleChange()}
                onBlur={this.handleChange()}
              >
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
                {this.state.listBarang.map((list) => (
                  <option
                    value={
                      list.kodeJenis +
                      "|" +
                      list.kadarCetak +
                      "|" +
                      list.kadarJual +
                      "|" +
                      list.namaJenis
                    }
                    key={list.kodeJenis}
                  >
                    {list.namaJenis}
                  </option>
                ))}
              </Field>
            </div>
            <div className="col-md-3 col-lg-3 form-group">
              <Field
                name="kadarTransaksi"
                label="Cap Kode"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group">
              <Field
                name="bruto"
                label="Bruto (Gr)"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group">
              <Field
                name="nettoTerima"
                label="Netto Terima"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group">
              <Field
                name="nettoKirim"
                label="Netto Kirim"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group">
              <Field
                name="nettoSelisih"
                label="Netto Selisih"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 form-group d-none">
              <Field
                name="keterangan"
                label="Netto Selisih"
                component={renderField}
                type="text"
                onBlur={this.setNetto()}
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-lg-12 col-md-12 mb-3 mt-3">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Tambah Data
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTerimaMasak = reduxForm({
  form: "FormTerimaMasak",
  enableReinitialize: true,
  validate: validaitonTerimaMasak,
})(FormTerimaMasak);
const selector = formValueSelector("FormTerimaMasak"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, nettoKirim, nettoTerima } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "nettoKirim",
    "nettoTerima"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    nettoSelisih: (
      parseFloat(nettoTerima ? nettoTerima : 0) -
      parseFloat(nettoKirim ? nettoKirim : 0)
    ).toFixed(3),
  };
})(FormTerimaMasak);
