const validaitonTerimaMasak = (value) => {
  const errors = {};
  if (!value.kodeBarang === "DEFAULT") {
    errors.kodeBarang = "Tidak Boleh kosong";
  } else if (!value.kodeBarang) {
    errors.kodeBarang = "Tidak Boleh Kosong";
  }
  if (!value.bruto) {
    errors.bruto = "Tidak Boleh kosong";
  }
  if (!value.nettoTerima) {
    errors.nettoTerima = "Tidak boleh kosong";
  }
  return errors;
};

export default validaitonTerimaMasak;
